import React from "react";
import { Button, Form, Input, Header, TextArea } from "semantic-ui-react";

export default function Dashboard3() {
  return (
    <div>
      <Header
        as="h2"
        image="data:image/jpeg;base64,"
        content="Create Post"
      />
      <hr />
      <Form>
        <Form.Group widths="equal">
          <Form.Field
            control={Input}
            label="First name"
            placeholder="First name"
          />
          <Form.Field
            control={Input}
            label="Last name"
            placeholder="Last name"
          />
        </Form.Group>

        <Form.Field
          control={TextArea}
          label="Post"
          placeholder="What's in your mind..."
        />
        <Form.Field>
          <Input action="Upload" placeholder="Upload image/video..." />
        </Form.Field>
        <Form.Field control={Button}>Share Post</Form.Field>
      </Form>
    </div>
  );
}
